import React, {
  useState,
  lazy,
  Suspense,
  useLayoutEffect,
  useMemo,
  useCallback,
  useEffect,
  useContext,
  useRef,
} from "react";
import { Tabs, ConfigProvider } from "antd";
import { useParams } from "react-router-dom";
import {
  HomeOutlined,
  MoonOutlined,
  HeartOutlined,
  PullRequestOutlined,
  BarChartOutlined,
  EnvironmentOutlined,
  LineChartOutlined,
  MessageOutlined,
  DatabaseOutlined,
  AlertOutlined,
  ContactsOutlined,
  MobileOutlined,
  FileTextOutlined,
} from "@ant-design/icons";

import "./style.css";
import SkeletonView from "./Utiles/Skeleton";
import { useLocation, useNavigate } from "react-router-dom";

// Lazy load components
const OverviewTab = lazy(() =>
  import("./Components/TabContents/Overview/OverviewTab")
);
const ReportsTab = lazy(() =>
  import("./Components/TabContents/Reports/ReportsTab")
);
const SleepTab = lazy(() => import("./Components/TabContents/Sleep/SleepTab"));
const HealthTab = lazy(() =>
  import("./Components/TabContents/Health/HealthTab")
);
const EmergencyContactsTab = lazy(() =>
  import("./Components/TabContents/EmergencyContacts/EmergencyContactsTab")
);
const DevicesTab = lazy(() =>
  import("./Components/TabContents/Devices/DevicesTab")
);
const DataAnalyzeTab = lazy(() =>
  import("./Components/TabContents/DataAnalyze/DataAnalyzeTab")
);
const TrendTab = lazy(() => import("./Components/TabContents/Trends/TrendTab"));
const ActivityTab = lazy(() =>
  import("./Components/TabContents/Activity/ActivityTab")
);
const LiveMapTab = lazy(() =>
  import("./Components/TabContents/LiveMap/LiveMapTab")
);
const CommunicationTab = lazy(() =>
  import("./Components/TabContents/Communication/CommunicationTab")
);
const HealthDataTab = lazy(() =>
  import("./Components/TabContents/HealthData/HealthDataTab")
);
const AlertHistoryTab = lazy(() =>
  import("./Components/TabContents/Alerts/AlertsHistoryTab")
);
import ls from "store2";
import { getSleepData, subscribeToMqtt } from "@/api/deviceReports";
import { getDetails } from "@/api/elderly";
import { CustomContext } from "@/Context/UseCustomContext";
import { SidebarContext } from "@/Context/CustomContext";
import dayjs from "dayjs";
export default function ElderlyProfile() {
  const { dailyRepDate } = useContext(SidebarContext);
  const [elderlyDetails, setElderlyDetails] = useState({});
  const [sleepData, setSleepData] = useState([]);
  const [size, setSize] = useState("small");
  const location = useLocation();
  const navigate = useNavigate();
  const params = useParams();

  const previousPathnameRef = useRef(location.pathname);
  // const [sleepData, setSleepData] = useState([]);
  // const elderlyDetails = ls.get("elderly_details");
  // Define tab items with their associated components and icons
  const tabItems = useMemo(
    () => [
      {
        label: "Overview",
        key: "overview",
        icon: <HomeOutlined />,
        component: <OverviewTab />,
      },
      {
        label: "Sleep",
        key: "sleep",
        icon: <MoonOutlined />,
        component: <SleepTab />,
      },
      {
        label: "Wellness Insights",
        key: "health",
        icon: <HeartOutlined />,
        component: <HealthTab />,
      },
      {
        label: "Activity",
        key: "activity",
        icon: <PullRequestOutlined />,
        component: <ActivityTab />,
      },
      // {
      //   label: "Data Analyze",
      //   key: "dataAnalyze",
      //   icon: <BarChartOutlined />,
      //   component: <DataAnalyzeTab />,
      // },
      {
        label: "Live Map",
        key: "liveMap",
        icon: <EnvironmentOutlined />,
        component: <LiveMapTab />,
      },
      {
        label: "Trends",
        key: "trends",
        icon: <LineChartOutlined />,
        component: <TrendTab />,
      },
      {
        label: "Communication",
        key: "communication",
        icon: <MessageOutlined />,
        component: <CommunicationTab />,
      },
      {
        label: "Personal Records",
        key: "healthData",
        icon: <DatabaseOutlined />,
        component: <HealthDataTab />,
      },
      {
        label: "Trusted Contacts",
        key: "emergencyContacts",
        icon: <ContactsOutlined />,
        component: <EmergencyContactsTab />,
      },
      {
        label: "Notifications History",
        key: "alertHistory",
        icon: <AlertOutlined />,
        component: <AlertHistoryTab />,
      },
      {
        label: "Devices",
        key: "devices",
        icon: <MobileOutlined />,
        component: <DevicesTab />,
      },
      {
        label: "Reports",
        key: "reports",
        icon: <FileTextOutlined />,
        component: <ReportsTab />,
      },
    ],
    []
  );

  // Get the initial tab from the query parameter or default to "overview"
  const queryParams = useMemo(
    () => new URLSearchParams(location.search),
    [location.search]
  );
  const initialTab = queryParams.get("tab") || "overview";
  const [activeTab, setActiveTab] = useState(initialTab);
  // Sync URL with activeTab state
  useLayoutEffect(() => {
    ls.set("activeElderlyTab", activeTab);
    navigate(`${location.pathname}?tab=${activeTab}`, { replace: true });
  }, [activeTab]);

  useEffect(() => {
    const queryParams = new URLSearchParams(location.search);
    const tab = queryParams.get("tab");
    if (tab && tab !== activeTab) {
      setActiveTab(tab);
    }
  }, [location.search]);

  const resizeCharts = useCallback(() => {
    const charts = document.querySelectorAll(".echarts");
    charts.forEach((chart) => {
      if (chart && chart.echartsInstance) {
        chart.echartsInstance.resize();
      }
    });
  }, []);

  useLayoutEffect(() => {
    const handleResize = () => resizeCharts();
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, [resizeCharts]);

  //get elderly details
  const getElderlyDetails = useCallback(() => {
    getDetails({ id: params.id })
      .then((res) => {
        const updatedElderlyDetails = {
          ...res?.data,
          // deviceId: "E598A28DB9EB",
          deviceId:
            res?.data?.rooms?.length > 0
              ? res.data.rooms
                  .filter((item) => item.device_no)
                  .map((item) => item.device_no)
                  .join(", ")
              : "",
        };

        setElderlyDetails(updatedElderlyDetails);
        ls.set("elderly_details", updatedElderlyDetails);
        subscribesToMqtt({
          uids:
            res?.data?.rooms?.length > 0
              ? res.data.rooms
                  .filter((item) => item.device_no)
                  .map((item) => item.device_no)
                  .join(", ")
              : "",
        });
        fatchSleepData({
          uids:
            res?.data?.rooms?.length > 0
              ? res.data.rooms
                  .filter((item) => item.device_no)
                  .map((item) => item.device_no)
                  .join(", ")
              : "",
          to_date: dayjs().subtract(1, "day").format("YYYY-MM-DD"),
        });
      })
      .catch((error) => {
        console.log("error ==================>", error);
      });
  }, [dailyRepDate]);
  useEffect(() => {
    ls.set("elderly_details", []);
    ls.set("sleepData", []);
    getElderlyDetails();
  }, [getElderlyDetails]);
  function fatchSleepData({ uids, to_date }) {
    getSleepData({
      uids: uids,
      elderly_id: params.id,
      to_date: dailyRepDate || to_date,
    })
      .then((res) => {
        setSleepData(res?.data);
        ls.set("sleepData", res?.data);
      })
      .catch((error) => {
        console.log("error ==================>", error);
      });
  }
  const subscribesToMqtt = useCallback(
    (data) => {
      const currentPathname = location.pathname;
      const isElderlyProfile = currentPathname.includes(
        "/supporter/elderlies/elderly-profile"
      );
      if (isElderlyProfile) {
        subscribeToMqtt({
          uid: data?.uids,
          messageType: ["1", "2", "3", "4"],
          topics: {
            pub: [data?.uids],
          },
        })
          .then((res) => {
            console.log(res);
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    [params.id]
  );
  const tabListRef = useRef(null);
  const [isDragging, setIsDragging] = useState(false);
  const [startX, setStartX] = useState(0);
  const [scrollLeft, setScrollLeft] = useState(0);

  const [isScrollableLeft, setIsScrollableLeft] = useState(false);
  const [isScrollableRight, setIsScrollableRight] = useState(true);

  const handleMouseDown = (e) => {
    if (e.button !== 0) return; // Only respond to the left mouse button
    setIsDragging(true);
    setStartX(e.pageX - tabListRef.current.offsetLeft);
    setScrollLeft(tabListRef.current.scrollLeft);
  };

  const handleMouseMove = (e) => {
    if (!isDragging) return;
    e.preventDefault();
    const x = e.pageX - tabListRef.current.offsetLeft;
    const walk = (x - startX) * 0.8; // Adjust multiplier for smoothness
    tabListRef.current.scrollLeft = scrollLeft - walk;

    // Update indicators with requestAnimationFrame for better performance
    requestAnimationFrame(updateScrollIndicators);
  };

  const handleMouseUp = () => setIsDragging(false);
  const handleMouseLeave = () => setIsDragging(false);

  const updateScrollIndicators = () => {
    if (!tabListRef.current) return;
    const { scrollLeft, scrollWidth, clientWidth } = tabListRef.current;
    setIsScrollableLeft(scrollLeft > 0);
    setIsScrollableRight(scrollLeft + clientWidth < scrollWidth);
  };

  useEffect(() => {
    updateScrollIndicators();
  }, [tabItems]);

  return (
    <div id="elderly-profile-tabs" className="rounded-2xl mt-8 relative">
      <CustomContext.Provider
        value={{ sleepData, elderlyDetails, getElderlyDetails }}
      >
        <ConfigProvider
          theme={{
            token: {
              fontFamily: "Baloo2",
              colorPrimary: "#252F67",
              colorLinkActive: "#252F67",
              colorLinkHover: "#252F67",
              colorLink: "#252F67",
            },
          }}
        >
          {isScrollableLeft && (
            <div
              style={{
                position: "absolute",
                left: 0,
                top: 0,
                bottom: 0,
                width: "30px",
                background:
                  "linear-gradient(to right, white, rgba(255,255,255,0))",
                zIndex: 1,
                pointerEvents: "none",
                borderRadius: "14px",
                height: "60px",
              }}
            />
          )}
          {isScrollableRight && (
            <div
              style={{
                position: "absolute",
                right: 0,
                top: 0,
                bottom: 0,
                width: "30px",
                background:
                  "linear-gradient(to left, white, rgba(255,255,255,0))",
                zIndex: 1,
                pointerEvents: "none",
                borderRadius: "14px",
                height: "60px",
              }}
            />
          )}
          <Tabs
            activeKey={activeTab}
            onChange={setActiveTab}
            size={size}
            className="mb-4"
            tabBarStyle={{ borderBottom: "none" }}
            animated={{ inkBar: false, tabPane: false }}
            renderTabBar={(props, DefaultTabBar) => (
              <div
                className="tabs-drag-container"
                ref={tabListRef}
                onMouseDown={handleMouseDown}
                onMouseMove={handleMouseMove}
                onMouseUp={handleMouseUp}
                onMouseLeave={handleMouseLeave}
                onScroll={updateScrollIndicators}
                style={{
                  overflowX: "auto",
                  borderRadius: "14px",
                  whiteSpace: "nowrap",
                  cursor: isDragging ? "grabbing" : "grab",
                  display: "flex",
                  scrollbarWidth: "none",
                  msOverflowStyle: "none",
                  userSelect: "none",
                }}
              >
                <style>
                  {`
                    .tabs-drag-container::-webkit-scrollbar {
                      display: none;
                    }
                  `}
                </style>
                <DefaultTabBar {...props} />
              </div>
            )}
          >
            {tabItems.map((item) => (
              <Tabs.TabPane
                tab={
                  <span className="flex items-center gap-2 !border-none">
                    {item.icon}
                    {item.label}
                  </span>
                }
                key={item.key}
              >
                <Suspense fallback={<SkeletonView />}>
                  {item.component}
                </Suspense>
              </Tabs.TabPane>
            ))}
          </Tabs>
        </ConfigProvider>
      </CustomContext.Provider>
    </div>
  );
}
